html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-color: #EEE !important;
}

#root {
  display: flex;
  flex-direction: column;
}

#dashboard {
  flex: 1 1;
  padding: 17px 16px 16px 16px;
  box-sizing: border-box;
  width: 100%;
  display: block;
  float: left;
}

.section {
  height: 100%;
  width: 100%;
  padding: 12px;
  background-color: #FAFAFA !important;
  box-sizing: border-box;
}

.bg {
  fill: #FAFAFA;
}

.axis line {
  stroke: #000;
}

.axis text {
  fill: #000;
}

.tooltip {
  z-index: 9999;  /* Appear over the app bar */
}

.tooltip-content {
  padding: 8px;
  color: #222;

  h3 {
    font-weight: normal;
    margin: 0;
    padding: 0 0 8px 0;
  }

  div {
    opacity: 0.75;
  }
}

.plot-label {
  fill: #202122;
  font-size: 16px;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #303030 !important;
  }
  .section {
    background-color: #424242 !important;
  }
  .bg {
    fill: #424242;
  }
  .axis line {
    stroke: #FFF;
  }
  .axis text, .plot-label {
    fill: #FFF;
  }
  .tooltip {
    background-color: #303030 !important;
  }
  .tooltip-content {
    color: #FFF;
  }
}

@media screen and (max-width: 719px) { /* Mobile */
  #dashboard main {
    height: 320px;
  }
  #dashboard main #main-plot {
    height: 100%;
  }

  #dashboard aside {
    padding-top: 10px;
    text-align: center;
  }
}

@media screen and (min-width: 720px) { /* Desktop */
  #root {
    height: 100%;
  }

  #dashboard main {
    padding-right: 8px;
    width: 100%;
    height: 100%;
    float: left;
    margin-right: -195px;
  }

  #dashboard main #main-plot {
    margin-right: 195px;
    height: 100%;
  }

  #dashboard aside {
    width: 195px;
    height: 100%;
    float: right;
  }

  #dashboard .controls > div {
    display: block;
  }
}